// Core
import { List, Map } from 'immutable';

// Instruments
import types from './types';

const initialState = Map({
  items: List(),
  pending: false,
  totalCount: 0,
  pageSize: 10,
  currentPage: 0,
});

export const hotelsKeys = {
  controller: 'hotels',
  items: 'items',
  pending: 'pending',
  totalCount: 'totalCount',
  pageSize: 'pageSize',
  currentPage: 'currentPage',
};

export const hotelsReducer = (state = initialState, action) => {
  const { type, payload } = action;
  switch (type) {
    case types.SET_HOTELS_LIST: {
      return state.set(hotelsKeys.items, payload);
    }
    case types.SET_HOTELS_CURRENT_PAGE: {
      return state.set(hotelsKeys.currentPage, payload);
    }
    case types.SET_HOTELS_PAGE_SIZE: {
      return state.set(hotelsKeys.pageSize, payload);
    }
    case types.SET_HOTELS_TOTAL_COUNT: {
      return state.set(hotelsKeys.totalCount, payload);
    }
    case types.SET_HOTELS_PENDING: {
      return state.set(hotelsKeys.pending, payload);
    }
    default:
      return state;
  }
};
