// Core
import React from 'react';
import { Field, reduxForm } from 'redux-form/immutable';
import clsx from 'clsx';
import * as PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
// Hooks
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';
// Ui
import Button from '@material-ui/core/Button';
import Drawer from '@material-ui/core/Drawer';
import Rating from '@material-ui/lab/Rating';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Typography from '@material-ui/core/Typography';
// Parts
import TextField from '../Form_Fields/TextField';
// Styles
import filterStyle from './filterStyle';
// Engine
import Selectors from '../../../engine/config/selectors';
import uiActions from '../../../engine/core/ui/action';
import hotelsActionAsync from '../../../engine/core/hotels/saga/action';
import { formFields, formName } from './form';
import { useStyles } from '../../_hooks/useStyles';

function Filter(props) {
  const theme = useTheme();
  const dispatch = useDispatch();
  const classes = useStyles(filterStyle);
  const matchesSmUp = useMediaQuery(theme.breakpoints.up('sm'));
  const hotelListsPending = useSelector(Selectors.hotels.pending);
  const hotelsFilterStatus = useSelector(Selectors.ui.hotelsFilterStatus);
  const { handleSubmit } = props;

  const handleFilterSearch = (filterData) => {
    dispatch(hotelsActionAsync.getHotelsAsync(filterData.toJS()));
    dispatch(uiActions.hotelsFilterStatus(false));
  };
  return (
    <Drawer
      className={classes.drawer}
      variant="persistent"
      anchor="left"
      open={matchesSmUp || hotelsFilterStatus}
      classes={{
        paper: clsx({
          [classes.paperMobile]: !matchesSmUp,
          [classes.paperDesktop]: matchesSmUp,
        }),
      }}
    >
      <form onSubmit={handleSubmit(handleFilterSearch)} className={classes.container}>
        <Typography gutterBottom variant="h5" component="h2">
          Filter Box
        </Typography>
        <Field
          component={TextField}
          name={formFields.name}
          id={formFields.name}
          label="Filter by hotel name"
          variant="outlined"
          color="secondary"
          className={classes.name}
        />
        <Typography component="legend">Filter by rating</Typography>
        <FormGroup>
          {[5, 4, 3, 2, 1].map((number) => (
            <FormControlLabel
              key={number.toString()}
              control={(
                <Checkbox />
              )}
              label={(
                <span className={classes.ratingWrapper}>
                  <Rating
                    name="simple-controlled"
                    value={number}
                    readOnly
                  />
                </span>
              )}
            />
          ))}
        </FormGroup>
        <Button disabled={hotelListsPending} variant="contained" color="primary" type="submit" fullWidth>Filter</Button>
      </form>
    </Drawer>
  );
}

Filter.propTypes = {
  handleSubmit: PropTypes.func,
};

Filter.defaultProps = {
  handleSubmit: () => {},
};

const formConfig = {
  form: formName,
};

export default reduxForm(formConfig)(Filter);
