// Core
import { Map } from 'immutable';

// Instruments
import types from './types';

const initialState = Map({
  hotelsFilters: false,
});

export const uiKeys = {
  controller: 'ui',
  hotelsFilters: 'hotelsFilters',
};

export const uiReducer = (state = initialState, action) => {
  const { type, payload } = action;
  switch (type) {
    case types.SET_HOTELS_FILTER_STATUS: {
      return state.set(uiKeys.hotelsFilters, payload);
    }
    default:
      return state;
  }
};
