// Core
import { getIn, List } from 'immutable';

// SelectorsKeys
import { hotelsKeys } from '../core/hotels/reducers';
import { uiKeys } from '../core/ui/reducers';

const hotels = Object.freeze({
  items: (state) => getIn(state, [hotelsKeys.controller, hotelsKeys.items], List()),
  pending: (state) => getIn(state, [hotelsKeys.controller, hotelsKeys.pending], false),
});

const ui = Object.freeze({
  hotelsFilterStatus: (state) => getIn(state, [uiKeys.controller, uiKeys.hotelsFilters], false),
});

const router = Object.freeze({
  pathname: (state) => getIn(state, ['router', 'location', 'pathname'], ''),
});

export default Object.freeze({
  router,
  hotels,
  ui,
});
