// Core
import { all, takeEvery } from 'redux-saga/effects';

// Parts
import asyncTypes from './asyncTypes';

// Workers
import { callGetHotelsListWorker } from './workers/getHotelsList';

function* watchGetHotelsList() {
  yield takeEvery(asyncTypes.GET_HOTELS_LIST_ASYNC, callGetHotelsListWorker);
}

export function* watchersHotels() {
  yield all([watchGetHotelsList()]);
}
