// Core
import { apply, put } from 'redux-saga/effects';
import { push } from 'connected-react-router';

// Engine
import routerLink from '../../../../config/routerLink';
import { api } from '../../../../config/api';
import hotelsActions from '../../action';

export function* callGetHotelsListWorker({ payload: params }) {
  yield put(hotelsActions.setHotelsPending(true));

  const response = yield apply(api, api.hotels.getHotelsList, [params]);

  if (response && response.status >= 200 && response.status < 400) {
    const { data, status } = response;
    switch (status) {
      case 200: {
        yield put(hotelsActions.setHotelsList(data.items));
        localStorage.setItem('hotelsList', JSON.stringify(data.items));
        yield put(hotelsActions.setHotelsTotalCount(data.totalCount));
        yield put(push(routerLink.hotels));
        break;
      }
      default: {
        break;
      }
    }
  }
  yield put(hotelsActions.setHotelsPending(false));
}
