// Core
import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { NavLink } from 'react-router-dom';
// Ui
import IconButton from '@material-ui/core/IconButton';
import FilterList from '@material-ui/icons/FilterList';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Hidden from '@material-ui/core/Hidden';
// Hooks
import { useStyles } from '../../_hooks/useStyles';
// Styles
import headerStyles from './headerStyles';
// Engine
import routerLink from '../../../engine/config/routerLink';
import uiActions from '../../../engine/core/ui/action';
import Selectors from '../../../engine/config/selectors';

function Header() {
  const classes = useStyles(headerStyles);
  const dispatch = useDispatch();
  const hotelsFilterStatus = useSelector(Selectors.ui.hotelsFilterStatus);
  const pathname = useSelector(Selectors.router.pathname);
  return (
    <AppBar position="static" color="transparent">
      <Toolbar className={classes.root}>
        <NavLink to="/" className={classes.logoWrapper}>
          <div title="TRAVOLTA" className={classes.logoIcon} />
          <Typography variant="h1" className={classes.logoTitle}>TRAVOLTA</Typography>
        </NavLink>
        <div className={classes.content}>
          {pathname === routerLink.hotels
          && (
          <Hidden smUp>
            <IconButton
              onClick={() => dispatch(uiActions.hotelsFilterStatus(!hotelsFilterStatus))}
            >
              <FilterList />
            </IconButton>
          </Hidden>
          )}
          <Hidden xsDown>
            <Button variant="contained" className={classes.button}>About</Button>
            <Button variant="contained" className={classes.button}>My Booking</Button>
            <Button variant="contained" className={classes.button}>Sign In</Button>
          </Hidden>
        </div>
      </Toolbar>
    </AppBar>
  );
}

export default Header;
