// Core
import 'date-fns';
import React from 'react';
/* eslint-disable */
// Hooks
import { useSelector } from 'react-redux';
// Ui
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import Container from '@material-ui/core/Container';
import { useStyles } from '../../_hooks/useStyles';
// Parts
import Filter from '../../components/Filter/Filter';
import Infinity from '../../components/Infinity/Infinity';
import Loading from '../../components/Loading/Loading';
// Styles
import hotelsListStyles from './hotelsListStyles';
// Engine
import Selectors from '../../../engine/config/selectors';

function HotelsList() {
  const classes = useStyles(hotelsListStyles);
  const storageItems = JSON.parse(localStorage.getItem('hotelsList'));
  const selectedItemsPending = useSelector(Selectors.hotels.pending);
  const selectedItems = useSelector(Selectors.hotels.items);
  const hotelItems = selectedItems.length ? selectedItems : storageItems;

  const itemComponent = hotelItems && hotelItems.length
    ?
      <Infinity itemsList={hotelItems} />
    : (
      <Card className={classes.notFoundMessage}>
          We could not find anything according to your request.
      </Card>
    );
  return (
      <Container component="main" maxWidth="lg" className={classes.container}>
        <Grid container spacing={3} className={classes.root}>
          <Filter />
          <Grid item xs={12} sm={7} md={8}>
            {selectedItemsPending ? <Loading isLoading /> : itemComponent}
          </Grid>
        </Grid>
      </Container>
  );
}

export default HotelsList;
