// Helpers
import actionCreator from '../../_helpers/actionCreator';
// Types
import types from './types';

const hotelsActions = Object.freeze({
  setHotelsList(hotelsList) {
    return actionCreator(types.SET_HOTELS_LIST, hotelsList);
  },
  setHotelsPending(status) {
    return actionCreator(types.SET_HOTELS_PENDING, status);
  },
  setHotelsTotalCount(count) {
    return actionCreator(types.SET_HOTELS_TOTAL_COUNT, count);
  },
  setHotelsCurrentPage(page) {
    return actionCreator(types.SET_HOTELS_CURRENT_PAGE, page);
  },
});

export default hotelsActions;
