// Core
import React, { useState } from 'react';
import * as PropTypes from 'prop-types';
import InfiniteLoading from 'react-simple-infinite-loading';
// Hooks
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';
import HotelsItem from '../HotelsItem/HotelsItem';

function InfiniteLoadingComponent({ itemsList }) {
  const theme = useTheme();
  const matchesSmUp = useMediaQuery(theme.breakpoints.up('sm'));
  const [items, setItems] = useState(itemsList.slice(0, 10));

  const loadMoreItems = () => {
    const newItems = itemsList.slice(items.length, items.length + 10);

    return new Promise((resolve) => {
      setTimeout(() => {
        setItems([...items, ...newItems]);
        resolve();
      }, 1000);
    });
  };

  return (
    <InfiniteLoading
      hasMoreItems
      itemHeight={matchesSmUp ? 160 : 110}
      loadMoreItems={loadMoreItems}
    >
      {items.map((item) => (
        <HotelsItem
          key={item.id}
          itemData={item}
        />
      ))}
    </InfiniteLoading>
  );
}

InfiniteLoadingComponent.propTypes = {
  itemsList: PropTypes.arrayOf(PropTypes.shape({})),
};

InfiniteLoadingComponent.defaultProps = {
  itemsList: [],
};

export default InfiniteLoadingComponent;
