// Core
import axios from 'axios';

const instance = axios.create({
  baseURL: '/mocks/',
  headers: { 'Content-Type': 'application/json' },
});

instance.interceptors.response.use(
  (response) => {
    const { config, data } = response;
    const { url, params } = config;
    // Mocks request. It`s mast return Server, after send params :)
    if (url === 'hotels.json') {
      const cityParams = params.searchForm_destination;
      const nameParams = params.filterForm_name;

      const searchData = data.filter((hotel) => {
        if (cityParams && hotel.city.match(cityParams)) {
          return hotel;
        }
        if (nameParams && hotel.name.match(nameParams)) {
          return hotel;
        }
        return null;
      });
      response.data = {
        items: cityParams || nameParams ? searchData : data,
        totalCount: searchData.length,
      };
    }

    return response;
  },
  (error) => {
    if (error.response) {
      const { data } = error.response;
      if (data.code >= 400) {
        console.error(data.code);
      }
    }
  },
);

export const api = Object.freeze({
  // Fetch
  hotels: {
    getHotelsList(params) {
      return instance.get('hotels.json', { params });
    },
  },
});
