// Core
import 'date-fns';
import React from 'react';
import * as PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import { reduxForm, Field } from 'redux-form/immutable';
// Ui
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
// Parts
import { Container } from '@material-ui/core';
import AutocompleteField from '../../components/Form_Fields/AutocompleteField';
import DatePickerField from '../../components/Form_Fields/DatePickerField';
import TextField from '../../components/Form_Fields/TextField';
import Footer from '../../components/Footer/Footer';
// Hooks
import { useStyles } from '../../_hooks/useStyles';
// Styles
import searchFormStyles from './searchFormStyles';
// Data and config
import appendix from './appendix';
import { formFields, formName } from './form';
// Engine
import hotelsActionAsync from '../../../engine/core/hotels/saga/action';
import { validators } from '../../../engine/_helpers/validation';
import Selectors from '../../../engine/config/selectors';

function SearchForm(props) {
  const { handleSubmit } = props;
  const dispatch = useDispatch();
  const hotelListsPending = useSelector(Selectors.hotels.pending);
  const classes = useStyles(searchFormStyles);

  const handleSubmitSearch = (searchData) => {
    dispatch(hotelsActionAsync.getHotelsAsync(searchData.toJS()));
  };

  return (
    <Container component="main" maxWidth="lg">
      <Paper className={classes.root}>
        <form onSubmit={handleSubmit(handleSubmitSearch)}>
          <Grid container spacing={3}>
            <Grid item xs={12} md={3}>
              <Field
                component={AutocompleteField}
                options={appendix}
                id={formFields.destination}
                label="Destination"
                color="secondary"
                validate={[validators.required]}
                validation
                name={formFields.destination}
              />
            </Grid>
            <Grid item xs={12} md={3}>
              <Field
                component={DatePickerField}
                color="secondary"
                id={formFields.checkIn}
                label="Check-in"
                name={formFields.checkIn}
              />
            </Grid>
            <Grid item xs={12} md={3}>
              <Field
                component={DatePickerField}
                color="secondary"
                id={formFields.checkOut}
                label="Check-out"
                name={formFields.checkOut}
              />
            </Grid>
            <Grid item xs={12} md={3}>
              <Grid container justify="space-between" alignItems="flex-end" spacing={3}>
                <Grid item xs={5} md={4}>
                  <Field
                    component={TextField}
                    id={formFields.adults}
                    name={formFields.adults}
                    type="number"
                    label="Adults"
                    color="secondary"
                  />
                </Grid>
                <Grid item xs={5} md={4}>
                  <Field
                    component={TextField}
                    id={formFields.children}
                    name={formFields.children}
                    type="number"
                    label="Children"
                    color="secondary"
                  />
                </Grid>
                <Grid item xs={12} md={4}>
                  <Button disabled={hotelListsPending} variant="contained" color="primary" type="submit" fullWidth>Search</Button>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </form>
      </Paper>
      <Footer />
    </Container>
  );
}

SearchForm.propTypes = {
  handleSubmit: PropTypes.func,
};

SearchForm.defaultProps = {
  handleSubmit: () => {},
};

const formConfig = {
  form: formName,
};


export default reduxForm(formConfig)(SearchForm);
